import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'oty-layout-options',
  templateUrl: './layout.options.component.html',
  styleUrls: ['./layout.options.component.scss']
})
export class LayoutOptionsComponent implements OnInit {
  options = ['grid','circle','cose-bilkent','concentric','breadthfirst'];
  @Output() changeLayout: EventEmitter<string> = new EventEmitter();
  @Output() zoom: EventEmitter<number> = new EventEmitter();
  @Output() center: EventEmitter<number> = new EventEmitter();
  @Output() onExpandNodes: EventEmitter<number> = new EventEmitter();
  @Output() onCollapseNodes: EventEmitter<number> = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }

  updateLayout(option:string){
    this.changeLayout.emit(option);
  }

  zoomIn() {
    this.zoom.emit(0.1);
  }

  zoomOut(){
    this.zoom.emit(-0.1);
  }

  centerLayout(){
    this.center.emit();
  }

  expandNodes(){
    this.onExpandNodes.emit();
  }

  collapseNodes(){
    this.onCollapseNodes.emit();
  }

}



