import {Component, Input, Output, EventEmitter, ElementRef, OnInit} from '@angular/core';
import {
  BotUserInputSelectableActionButtons,
  CheckboxList,
  ConversationHistory
} from "../models/response.model";
import {ConversationService} from "../services/conversation.service";
import {ISubscription} from "rxjs/Subscription";

@Component({
  selector: 'oty-conversation-history',
  templateUrl: './conversation-history.component.html',
  styleUrls: [
    './conversation-history.component.scss',
    '../ontology.conversation/conversation.component.main.scss']
})
export class ConversationHistoryComponent implements OnInit {
  private subscriptions: ISubscription[] = [];
  @Input() conversationHistory: ConversationHistory[];
  @Input() buttonActions: BotUserInputSelectableActionButtons[];
  @Input() multiselectOptions: CheckboxList[];

  @Output() onSendOption: EventEmitter<string> = new EventEmitter();
  @Output() onSendMultiOptions: EventEmitter<number[]> = new EventEmitter();

  constructor(private myElement: ElementRef,
              private conversationService:ConversationService) {
  }

  ngOnInit(){
    this.subscriptions.push(this.conversationService.scrollConversationHistoryList.subscribe(() => {
      this.scrollToBottom();
    }));
  }

  ngOnDestroy() {
    this.subscriptions.forEach((subscription: ISubscription) => subscription.unsubscribe());
  }
  scrollToBottom() {
    setTimeout(() => {
      this.myElement.nativeElement.scrollTop = this.myElement.nativeElement.scrollHeight;
    },0);
  }

  sendButtonOption(option: string) {
    this.onSendOption.emit(option);
  }

  sendMultiOptions() {
    let selectedItem = [];
    this.multiselectOptions.forEach((item: CheckboxList) => {
      if (item.checked) {
        selectedItem.push(item.value);
      }
    });
    this.onSendMultiOptions.emit(selectedItem);
  }

}



