import { NgModule }              from '@angular/core';
import { AngularSplitModule }    from 'angular-split';

import { ProjectComponent }      from './project.component';
import { ProjectRoutingModule }  from './project-routing.module';

import { LayoutModule }          from '../ontology.layout/layout.module';
import { LayoutDetailModule }    from '../ontology.layout.detail/layout.detail.module';
import { ConversationModule }      from '../ontology.conversation/conversation.module';
import {SharedModule} from "../common/shared.module";
import {UnfinishedTopicModule} from "../ontology.unfinished.topic/unfinished.topic.module";

@NgModule({
  imports:      [
    AngularSplitModule,
    ProjectRoutingModule,
    LayoutModule,
    LayoutDetailModule,
    ConversationModule,
    SharedModule,
    UnfinishedTopicModule],
  declarations: [ ProjectComponent ]
})
export class ProjectModule { }



