import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';

import {CytoscapeService} from '../services/cytoscape.service';
import * as cytoscape from 'cytoscape/dist/cytoscape';
import {ActivatedRoute} from "@angular/router";
import {ProjectService} from "../services/project.service";
import {HttpClient} from "@angular/common/http";
import {ISubscription} from "rxjs/Subscription";
import {ConversationService} from "../services/conversation.service";
import {LayoutService} from "./layout.service";
import {ToggleDetailEvent} from "../models/response.model";

@Component({
  selector: 'oty-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['layout.component.main.scss']
})
export class LayoutComponent implements OnInit, OnDestroy {
  @ViewChild('cy') cyElement: ElementRef;

  subscriptions: ISubscription[] = [];
  cy: cytoscape;
  projectId: string;
  isShowSidePanel: boolean;
  isShowSideIcon: boolean;

  constructor(private cyService: CytoscapeService,
              private projectService: ProjectService,
              private conversationService: ConversationService,
              private layoutService: LayoutService,
              private router: ActivatedRoute,private http: HttpClient) {
    this.router.params.subscribe(params => {
      this.projectId = params['id'];
    });
    this.isShowSidePanel = false;
    this.isShowSideIcon = false;
  }

  ngOnInit() {
    this.initCytoscape();

    this.subscriptions.push(this.conversationService.getGraphData(this.projectId).subscribe((graphData) => {
      if (graphData !== null && graphData.elements !== null) {
        this.cyService.cy.json(graphData);
        this.cyService.expandAll();
      }
    }));
    this.subscriptions.push(this.layoutService.toggleDetailPanelEvent.subscribe((event: ToggleDetailEvent) => {
      if (event.isRightSide) {
        this.toggleDetailPanel(event.isShow);
      }
    }));
    this.subscriptions.push(this.cyService.event.subscribe(event => {
      if (event.name === "onNothingClick") {
        this.isShowSideIcon = false;
      }
    }));
  }

  ngOnDestroy() {
    this.subscriptions.forEach((subscription: ISubscription) => subscription.unsubscribe());
  }

  initCytoscape(): void {
    this.cy = this.cyService.initCytoscape(this.cyElement.nativeElement);
  }
  resizeCytoscape(){
    this.cyService.resize();
  }

  updateLayout(option:string){
    this.cyService.updateLayout(option)
  }

  zoom(zoomNumber:number){
    this.cyService.zoom(zoomNumber);
  }

  collapseNodes(){
    this.cyService.collapseAll();
  }

  expandNodes(){
    this.cyService.expandAll();
  }

  center(){
    this.cyService.center();
  }

  toggleDetailPanel(isShowSidePanel: boolean){
    this.isShowSidePanel = isShowSidePanel;
    if (isShowSidePanel) {
      this.isShowSideIcon = true;
    }
    setTimeout(()=>{
      this.resizeCytoscape();
      this.center();
    }, 10);
  }
}



