import { NgModule }               from '@angular/core';
import { SharedModule }     from '../common/shared.module';

import { DragDropModule }         from 'primeng/dragdrop';
import { InputTextModule }        from 'primeng/inputtext';
import { ButtonModule }           from 'primeng/button';
import {RadioButtonModule} from 'primeng/radiobutton';


import { LayoutDetailComponent }        from './layout.detail.component';
import {CommonModule} from "@angular/common";

@NgModule({
  imports:      [ CommonModule, SharedModule, DragDropModule, ButtonModule, InputTextModule, RadioButtonModule ],
  exports:      [ LayoutDetailComponent ],
  declarations: [ LayoutDetailComponent ],
  providers: [ ]
})
export class LayoutDetailModule { }



