import { NgModule }               from '@angular/core';
import { CommonModule }           from '@angular/common';
import { FormsModule }            from '@angular/forms';

import { DragDropModule }         from 'primeng/dragdrop';

import { LayoutComponent }        from './layout.component';
import {LayoutOptionsModule} from "../ontology.layout.options/layout.options.module";
import { LayoutDetailModule }    from '../ontology.layout.detail/layout.detail.module';

@NgModule({
  imports:      [ CommonModule, FormsModule, DragDropModule, LayoutOptionsModule, LayoutDetailModule],
  exports:      [ LayoutComponent ],
  declarations: [ LayoutComponent ],
  providers: [ ]
})
export class LayoutModule { }



